body {
font-family: 'Righteous', comic-sans;
  background: url('./guitar.gif') no-repeat center center;
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}
.nav-bar-shadow {
  -webkit-box-shadow: 0px 9px 5px 0px rgba(0,0,0,0.75);
          box-shadow: 0px 9px 5px 0px rgba(0,0,0,0.75);
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.margin-auto {
  margin: auto;
}

.label, .input {
  font-size: 24px;
}

.input {
  font-family: 'Righteous', comic-sans;
}

.top-margin{
  margin: 30px 0px;
}

.some-padding {
  padding: 10px;
}
.opacity {
 background-color:rgba(247, 247, 247, .4);
}
.all-margin {
  margin: 30px 20px;
}

.submit {
  margin-top: 20px;
  width: 150px;
  font-size: 26px;
  font-family: 'Righteous', comic-sans;
  margin: auto;

}
.bottom-margin {
  margin-bottom: 20px;
}
.border {
  border: 2px solid black;
  border-radius: 4px;
}



